<template>
  <div id="signupInfo">
    <h2>Sign Up</h2>
    <h3>Please enter your details</h3>
    <table>
      <tr>
        <td>Name: </td>
        <td>
          <input
          v-model="name"
          type="text"
          placeholder="Full Name">
        </td>
      </tr>
      <tr>
        <td>Email: </td>
        <td>
          <input
          v-model="email"
          :class="{inputError: !emailValid}"
          type="email"
          placeholder="email address">
        </td>
      </tr>
      <tr>
        <td colspan="2">
          <p
          v-show="!emailValid"
          class="alert">Please enter a valid email address
          </p>
        </td>
      </tr>
      <tr>
        <td>
          Username:
        </td>
        <td>
          <input
          v-model="username"
          type="text"
          :class="{inputError: !userAvailable}"
          placeholder="Username">
        </td>
      </tr>
      <tr>
        <td colspan="2">
          <p
          v-show="!userAvailable"
          class="alert">
            This username is already taken
          </p>
        </td>
      </tr>
      <tr>
        <td>
          Password:
        </td>
        <td>
          <input
          v-model="password"
          type="password"
          :class="{inputError: !length}"
          placeholder="password">
        </td>
      </tr>
      <tr>
        <td colspan="2">
          <p
          v-show="!length"
          class="alert">
            Password should be of atleast 8 characters
          </p>
        </td>
      </tr>
      <tr>
        <td>Confirm Password:</td>
        <td>
          <input
          type="password"
          v-model="confirmPassword"
          :class="{inputError: !confirmPasswordMatch}"
          placeholder="Confirm password">
        </td>
      </tr>
      <tr>
        <td colspan="2">
          <p
          v-show="!confirmPasswordMatch"
          class="alert">
            Confirm password does not match password
          </p>
        </td>
      </tr>
      <tr>
        <td>Mobile No.:</td>
        <td>
          <input
          v-model="mobileNo"
          type="text"
          :class="{inputError: !mobileLength}"
          placeholder="mobile no.">
        </td>
      </tr>
      <tr>
        <td colspan="2">
          <p
          v-show="!mobileLength"
          class="alert">
            Mobile number should be of exactly 10 digits
          </p>
        </td>
      </tr>
    </table>
    <button
    @click="validate"
    class="button">
      Submit
    </button>
  </div>
</template>

<script>
export default {
  data() {
    return {
      username: '',
      password: '',
      name: '',
      confirmPassword: '',
      confirmPasswordMatch: true,
      email: '',
      mobileNo: '',
      userAvailable: true,
      length: true,
      emailValid: true,
      mobileLength: true,
      submitClicked: false,
    };
  },
  methods: {
    validate() {
      if (this.checkUsername() &&
      this.checkPassword() &&
      this.checkEmail() &&
      this.checkMobile()) {
        console.log("here");
        this.submitClicked = true;
        this.$router.push({name: 'signin',query: {name: this.username, password: this.password, email: this.email, mobile: this.mobileNo}})
      }
    },
    checkUsername() { 
      if (this.username !== '' ||
      this.name !== ''||
      this.confirmPassword !== ''||
      this.password !== ''||
      this.mobileNo !== ''||
      this.email !== '') {
        return true;
      } else {
        alert('All fields are compulsary');
        return false;
      }
    },
    checkPassword() {
      if (this.password === this.confirmPassword) {
        if (this.password.length < 8) {
          this.confirmPasswordMatch = true;
          this.length=false;
          return false;
        } else {
          this.length=true;
          return true;
        }
      } else {
        this.confirmPasswordMatch = false;
        return false;
      }
    },
    checkEmail() {
      const mail = this.email;
      if (!mail.includes('@')) {
        this.emailValid = false;
        return false;
      }
      else {
        this.emailValid = true;
        return true;
      }
    },
    checkMobile(){
      if (this.mobileNo.length!==10) {
        this.mobileLength = false;
        return false;
      } else {
        this.mobileLength = true;
        return true;
      }
    },
  },
  beforeRouteLeave (to, from, next) {
    if(!this.submitClicked){
        const answer = window.confirm(
        'Do you really want to leave? you have unsaved changes!');
      if (answer) {
        next()
      } else {
        next(false)
      }
    } else {
      next()
    }
  },
}
</script>

